<template>
<div class="detail">
  <div class="demo-spin-container" v-if="!detailImage || !detailImage.key">
    <Spin size="large" fix></Spin>
  </div>

  <div v-else>
    <div class="detail__img">
      <img :src="imageUrl" @load="$emit('imageload')">
    </div>
    <div class="detail__info">
      <div class="detail__name">{{ detailImage.key.split('/')[detailImage.key.split('/').length - 1] }}</div>
      <div class="detail__url"><span>url:</span> {{ imageUrl }}</div>
      <div :class="`detail__${detailKey}`" v-for="detailKey in Object.keys(detailImage)" :key="detailKey"><span>{{detailKey}}:</span> {{ detailImage[detailKey] }}</div>

      <div class="detail__operating">
        <ButtonGroup>
          <Button type="ghost" :loading="delLoading" @click="delImage">
            <span v-if="!delLoading"></span>
            <span v-else>Loading...</span>
          </Button>
          <Button type="ghost" @click="moveImage"></Button>
        </ButtonGroup>

        <div style="margin-top:20px;">
          <a :href="imageUrl" download><Button type="ghost" icon="ios-cloud-download-outline"></Button></a>
          <a :href="imageUrl" target="_blank"><Button type="ghost" icon="eye"></Button></a>
          <Button type="primary" id="copyBtn" icon="ios-copy"></Button>
        </div>
      </div>
    </div>

  </div>

  <QimModal :isShow="showModal" title="" @ok="saveMove" :image="detailImage" type="move" :loading="modalLoading" @closeModal="closeModal" />
</div>
</template>
<script>
import {
  mapState,
  mapMutations
} from 'vuex'

import {
  ajax
} from '@util'

import Clipboard from 'clipboard'

export default {
  props: {
    detailImage: {
      type: Object
    }
  },
  data () {
    return {
      delLoading: false,
      showModal: false,
      modalLoading: false
    }
  },
  computed: {
    ...mapState([
      'currentBucket',
      'openPrefixs'
    ]),
    imageUrl () {
      return this.currentBucket.domain + this.detailImage.key
    }
  },
  methods: {
    ...mapMutations([
      'deleteImage'
    ]),
    closeModal () {
      this.showModal = false
    },
    delImage () {
      this.delLoading = true
      var filename = this.detailImage.key

      this.$Modal.confirm({
        title: '',
        content: ': ' + '<span style="color:red;">' + filename + '</span>',
        loading: true,
        onOk: async () => {
          let data = {
            key: this.detailImage.key,
            bucket: this.currentBucket.bucket
          }
          await ajax.post('/api/delImage', data)
          this.delLoading = false
          this.deleteImage(this.detailImage.key)
          this.$Message.success('')
          this.$Modal.remove()
          this.$emit('deleteImage')
        },
        onCancel: () => {
          this.delLoading = false
        }
      })
    },
    moveImage () {
      this.showModal = true
    },
    async saveMove (moveTo) {
      this.modalLoading = true
      if (moveTo === this.detailImage.key) {
        this.modalLoading = false
        this.showModal = false
        return
      }

      ajax.post('/api/moveImage', {
        bucket: this.currentBucket.bucket,
        key: this.detailImage.key,
        newKey: moveTo
      })

      this.modalLoading = false
      this.showModal = false

      location.reload()
    }
  },
  mounted () {
    let clipboard = new Clipboard('#copyBtn', {
      text: () => this.imageUrl
    })
    clipboard.on('success', e => {
      this.$Message.success(' ' + this.imageUrl)
    })
  }
}
</script>
<style lang="less" scoped>
.detail {
    background: #fff;
    font-size: 14px;
    color: #666;
    padding: 10px;
    box-sizing: border-box;
    position: relative;
    min-height: 100px;
    transition: top 0.5s;
    top: 0;
    max-height: calc(100vh - 210px);
    overflow-y: scroll;
    & > div {
        word-break: break-all;
        margin-bottom: 10px;
        span {
            color: #333;
        }
    }
    &__img {
        img {
            width: 100%;
        }
    }
    &__name {
        font-size: 18px;
        color: red;
    }
    &__operating {
        text-align: center;
    }
}
</style>



