<template>
  <Upload
    multiple
    type="drag"
    :on-progress="handleProgress"
    :on-success="handleSuccess"
    :on-error="handleError"
    :format="['jpg','jpeg','png']"
    :on-format-error="handleFormatError"
    :before-upload="beforeUpload"
    :data='form'
    action="http://upload.qiniu.com/">
    <div style="padding: 20px 0">
      <Icon type="ios-cloud-upload" size="52" style="color: #3399ff"></Icon>
      <p> <span style="color: #f90;">{{ openPrefixs.join('/') + '/' + newPrefix }}</span> ()</p>
    </div>
  </Upload>
</template>
<script>
import {
  ajax
} from '@util'

import {
  mapState
} from 'vuex'

export default {
  props: ['newPrefix'],
  data () {
    return {
      form: {
        token: ''
      }
    }
  },
  computed: {
    ...mapState([
      'currentBucket',
      'openPrefixs'
    ])
  },
  watch: {
    currentBucket () {
      this.getUploadToken()
    }
  },
  methods: {
    handleFormatError (file) {
      this.$Notice.warning({
        title: '',
        desc: 'File format of ' + file.name + ' is incorrect, please select jpg or png.'
      })
    },
    beforeUpload (file) {
      this.form.key = `${this.openPrefixs.length ? `${this.openPrefixs.join('/')}/${this.newPrefix}` : ''}${file.name}`
    },
    handleProgress (event, file, fileList) {
      // this.loaded = (event.loaded / 1000000).toFixed(2)
      // this.fileSize = (event.total / 1000000).toFixed(2)
      // this.percent = (event.loaded / event.total * 100).toFixed(2)
      // console.log((event.loaded / event.total * 100).toFixed(2))
    },
    handleSuccess (response, file, fileList) {
      response.mimeType = 'image'
      response.key = response.key.split('/').pop()
      fileList.splice(fileList.indexOf(file), 1)
      this.$Message.success('')
      this.$emit('uploadfinish', {
        file: response,
        newPrefix: this.newPrefix || ''
      })
    },
    handleError (errorInfo, response, file) {
      this.$Message.error('')
      this.$Notice.error({
        title: 'errorInfo',
        desc: response.error + ': ' + file.name,
        duration: 0
      })
    },
    async getUploadToken () {
      // token
      const {
        code,
        uploadToken
      } = await ajax.get(`/api/uploadToken?bucket=${this.currentBucket.bucket}`)
      if (code === 1) {
        this.form.token = uploadToken
      }
    }
  }
}
</script>
<style lang="less" scoped>
</style>



