<template>
<div class="top">
  <Select v-model="bucketName" style="width:150px" @on-change="changeBucket">
    <Option :value="item.bucket" v-for="item in buckets" :key="item.bucket" >{{ item.bucket }}</Option>
  </Select>

  <Button class="add-bucket" type="primary" @click="showModal=true">Bucket</Button>

  <Breadcrumb>
    <BreadcrumbItem v-for="item in openPrefixs" :key="item">{{item}}</BreadcrumbItem>
  </Breadcrumb>

  <span v-if="openPrefixs.length > 0">
    <span class="ivu-breadcrumb-item-separator">/</span>
  </span>

  <Tooltip slot="append" content=": image/jpg/" placement="right">
    <Input
      v-model="newPrefix"
      placeholder=""
      style="width: 200px"
      @on-change="$emit('inputNewPrefix', newPrefix)"
      >
    </Input>
  </Tooltip>
  <!-- div.multiple -->

  <div class="switch">
    <ButtonGroup v-if="multipleSwitch">
      <!-- <Button></Button>
                <Button></Button> -->
      <Button type="error" @click="del"></Button>
    </ButtonGroup>

    <template v-if="multipleSwitch">
      <div class="switch__label"></div>
      <i-switch v-model="chooseAllSwitch" @on-change="chooseAll"></i-switch>
    </template>

    <div class="switch__label"></div>
    <i-switch v-model="multipleSwitch" @on-change="$emit('switchChange',multipleSwitch)"></i-switch>
  </div>

  <QimModal :isShow="showModal" title="Bucket" @ok="addBucket" type="addBucket" :loading="modalLoading" @closeModal="closeModal" />
</div>
</template>
<script>
import {
  mapState,
  mapMutations
} from 'vuex'

import {
  ajax
} from '@util'

export default {
  data () {
    return {
      showModal: false,
      modalLoading: false,
      bucketName: '',
      multipleSwitch: false,
      chooseAllSwitch: false,
      newPrefix: ''
    }
  },
  computed: {
    ...mapState([
      'openPrefixs',
      'buckets',
      'currentBucket',
      'buckets',
      'imageList',
      'multipleSwitchFile'
    ])
  },
  methods: {
    ...mapMutations([
      'setBuckets',
      'setCurrentBucket',
      'emptyMultipleSwitchFile',
      'deleteImage',
      'changeMultipleSwitchFile',
      'chooseAllMultipleSwitchFile'
    ]),
    chooseAll () {
      this.chooseAllMultipleSwitchFile(this.chooseAllSwitch)
    },
    del () {
      this.$Modal.confirm({
        title: '',
        content: '',
        loading: true,
        onOk: async () => {
          let data = {
            key: this.multipleSwitchFile,
            bucket: this.currentBucket.bucket
          }
          await ajax.post('/api/delImage', data)
          this.multipleSwitchFile.forEach(item => {
            this.deleteImage(item)
          })
          this.$Message.success('')
          this.emptyMultipleSwitchFile()
          this.$Modal.remove()
          this.$emit('deleteImage')
        },
        onCancel: () => {
          this.delLoading = false
        }
      })
    },
    changeBucket () {
      var bucket = this.bucketName
        ? this.buckets.find(item => {
          return item.bucket === this.bucketName
        }) : this.buckets[0]
      this.setCurrentBucket(bucket)
      this.emptyMultipleSwitchFile()
      this.$emit('getList')
    },
    addBucket (payload) {
      this.modalLoading = true
      this.setBuckets(payload)
      this.setCurrentBucket(payload)
      this.modalLoading = false
      this.showModal = false
      this.emptyMultipleSwitchFile()
      this.$emit('getList')
    },
    closeModal () {
      this.showModal = false
    }
  },
  watch: {
    currentBucket (val, oldVal) {
      this.bucketName = this.currentBucket ? this.currentBucket.bucket : ''
    },
    openPrefixs () {
      this.newPrefix = ''
      this.$emit('inputNewPrefix', this.newPrefix)
    }
  },
  mounted () {
    if (this.buckets.length === 0) {
      this.showModal = true
    }
    this.bucketName = this.currentBucket ? this.currentBucket.bucket : ''
  }
}
</script>
<style lang="less" scoped>
.top {
    width: 1200px;
    margin: 0 auto;
    padding: 10px 15px 0;
    &:after {
        content: '';
        display: block;
        clear: both;
    }
}

.ivu {
  &-select {
    float: left;
    margin-right: 20px;
  }
  &-breadcrumb {
    float: left;
    line-height: 32px;
  }
}

.switch {
  float: right;
  height: 32px;
  line-height: 32px;
  &__label {
    display: inline-block;
    margin-left: 20px;
  }
}
.add-bucket {
  float: left;
  margin-right: 20px;
}
</style>



